}
.HomeContent_numb__1JzMr {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  font-size: 18px;
  font-weight: 500;
  color: #111;
}
.HomeContent_numb-title__2d-fC {
  font-size: 12px;
}
.HomeContent_bar__3qf5Q {
  position: absolute;
  height: 100%;
  width: 100%;
  background: #fff;
  -webkit-border-radius: 100%;
  clip: rect(0px, 100px, 100px, 50px);
}
.HomeContent_progress__3GJh1 {
  position: absolute;
  height: 100%;
  width: 100%;
  -webkit-border-radius: 100%;
  clip: rect(0px, 50px, 100px, 0px);
}
.HomeContent_progress__3GJh1,
.HomeContent_dot__2KY7j span,
.HomeContent_zero-dot__2Najb span {
  background: #ffdc35;
}
.HomeContent_left__2AaN1.HomeContent_progress__3GJh1 {
  z-index: 1;
  animation: HomeContent_left__2AaN1 4s linear both;
}
@keyframes HomeContent_left__2AaN1 {
  100% {
    transform: rotate(180deg);
  }
}

.HomeContent_bar__3qf5Q.HomeContent_right__1Zy_G {
  z-index: 3;
  transform: rotate(180deg);
}
.HomeContent_right__1Zy_G.HomeContent_progress__3GJh1 {
  animation: HomeContent_right__1Zy_G 4s linear both;
  animation-delay: 4s;
}
@keyframes HomeContent_right__1Zy_G {
  100% {
    transform: rotate(135deg);
  }
}

.HomeContent_dot__2KY7j {
  z-index: 2;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 50%;
  height: 10px;
  margin-top: -5px;
  animation: HomeContent_dot__2KY7j 8s linear both;
  transform-origin: 0% 50%;
}
.HomeContent_zero-dot__2Najb {
  z-index: 100;
  position: absolute;
  top: 5px;
  left: 55%;
  height: 10px;
  margin-top: -5px;
  transform-origin: 0% 50%;
}
.HomeContent_zero-dot__2Najb span,
.HomeContent_dot__2KY7j span {
  position: absolute;
  right: 0;
  width: 10px;
  height: 10px;
  border-radius: 100%;
}

@keyframes HomeContent_dot__2KY7j {
  0% {
    transform: rotate(-90deg);
  }
  50% {
    transform: rotate(90deg);
    z-index: 4;
  }
  100% {
    transform: rotate(225deg);
    z-index: 4;
  }
}

